.info
{
  text-align: center;
  margin-top: 90px;
  font-size: 18px;
}

.ball
{
  background-color: black;
  color: white;
  text-align: center;
  font-family: monospace;

  border-radius: 50%;
  height: 280px;
  width: 280px;
  -webkit-box-shadow: 0 50px 10px rgba(0,0,0,0.3), 0 30px 50px rgba(0,0,0,0.3);
          box-shadow: 0 50px 10px rgba(0,0,0,0.3), 0 30px 50px rgba(0,0,0,0.3);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  padding: 50px;
}

.ball .inputBox
{
  position: relative;
}

.ball .inputBox input
{
  text-align: center;
  width: 100%;
  padding: 80px 0 10px;
  font-size: 16px;
  color: #fff;
  letter-spacing: 1px;
  margin-bottom: 1px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}

.answer
{
    font-size: 24px;
}

.fadeIn
{
  opacity: 0;
  -webkit-animation: fadeIn 2s forwards;
          animation: fadeIn 2s forwards;
}

@-webkit-keyframes fadeIn
{
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeIn
{
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
  }
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}



